.cards {
  padding-top: 2rem
}

h1 {
  text-align: center;
}

.cards__container__detail {
  display: inherit;
  flex-flow: column;
  align-items: center;
  max-width: 1050px;
  width: 90%;
  margin: 0 auto;
}

.cards__container__tutorial {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1200px;
  width: 90%;
  margin: 0 auto;
}

.cards__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1050px;
  width: 90%;
  margin: 0 auto;
}

.cards__wrapper {
  position: relative;
  margin: 40px 0 45px;
}

.cards__items {
  margin-bottom: 24px;
}

.cards__item {
  display: flex;
  flex: 1;
  margin: 0 1rem;
  border-radius: 10px;
  background:#fff;
}

.cards__item__paper {
  padding: 30px;
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.cards__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
  height: 100%;
}

.cards__item__pic__tutorial-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  background-color: #cb6768;
  box-sizing: border-box;
}

.cards__item__img__tutorial {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: scale-down;
  transition: all 0.2s linear;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: scale-down;
  transition: all 0.2s linear;
}

.cards__item__img:hover {
  transform: scale(1.1);
}

.cards__item__btn {
  text-align: center;
}

.cards__item__title {
  color: #252e48;
  font-size: 36px;
  line-height: 32px;
  text-align: center;
}

.cards__item__subtitle {
  color: #252e48;
  font-size: 32px;
  line-height: 32px;
  text-align: left;
}

.cards__item__body {
  color: #252e48;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  margin-bottom: 2vh;
}

.cards__item__info {
  padding: 2vh;
}

.cards__item__body.primary {
  color: #cb6768;
}

.cards__item__text {
  color: #252e48;
  font-size: 18px;
  line-height: 24px;
  text-align: justify;
}

.icon__tutorial {
  margin-right: 7px;
}

.table__detail {
  min-width: "300px";
}

.table__cell {
  font-weight: "bold",
}

@media only screen and (min-width: 1200px) {
  .content__blog__container {
    width: 84%;
  }
}

@media only screen and (min-width: 1024px) {
  .cards__items {
    display: flex;
  }
}

@media only screen and (max-width: 1024px) {
  .cards__item {
    margin-bottom: 2rem;
  }
}

@media only screen and (max-width: 768px) {
  .cards__item__title {
    font-size: 32px;
    line-height: 24px;
    text-align: center;
  }
  
  .cards__item__subtitle {
    color: #252e48;
    font-size: 24px;
    line-height: 24px;
    text-align: justify;
  }

  .cards__item__text {
    color: #252e48;
    font-size: 14px;
    line-height: 16px;
    text-align: justify;
  }

  .cards__container__detail {
    display: inherit;
  }

}




